import "./timeline.css"
import {ResumeModel} from "../../model/ResumeModel";
import {TimelineCard} from "./timelinecard/TimelineCard";
import {ReactNode} from "react";

let array: ResumeModel[] = [
    new ResumeModel("Arhs-Consulting", "2020-", "Programmer - Analyst Programmer"),
    new ResumeModel("Kyu Solutions", "2019-2020", "Analyst Developer"),
    new ResumeModel("Institut Français des Affaires", "2019", "RNCP level 6 - Bachelor equivalent"),
    new ResumeModel("Acadomia", "2018-2019", "Teacher in Mathematics and Computing Science"),
    new ResumeModel("First Year of Master in Mathematics", "2018", ""),
    new ResumeModel("Bachelor in Mathematics", "2017", "")
];

function alternateEntries(entries: ResumeModel[]): ReactNode {
    return entries.map((resumeCard, index) => {
        let isIndexEven = (index % 2) === 0;
        return (
            <div className={"timelineRow-right"}>
                <div className={"timelineEntry-right"}>
                    {
                        isIndexEven ? <TimelineCard card={resumeCard}/> : ""
                    }
                </div>
                <div className={"timelineEntry-left"}>
                    {
                        isIndexEven ? "" : <TimelineCard card={resumeCard}/>
                    }
                </div>
            </div>
        );

    });
}

export const Timeline = () => {
    return (
        <div className="timelineContainer row">
            {
                alternateEntries(array)
            }
        </div>

    );
}
