import React, {useEffect, useState} from "react";
import {ProjectModel} from "../../model/ProjectModel";
import {ProjectCard} from "./project-card/ProjectCard";
import "./project.css"

export const Projects = () => {

    let array: ProjectModel[] = [
        new ProjectModel("Portfolio", "2021", "The website you are browsing. It is written with React.js and TypeScript."),
        new ProjectModel("Chat", "2021", "A little application made with Spring Boot for the backend, and React.js & TypeScript for the frontend. This application is inspired by Tania Rascia's work and famous software Discord."),
        new ProjectModel("Blog", "2021", "This blog is written with PHP and Wordpress. Its purpose is to be a reminder about things I learned during the years.")
    ];

    const [search, setSearch] = useState('');
    const [projectArray, setProjectArray] = useState(array);

    const handleChange = (e: any) => {
        setSearch(e.target.value);
    }

    useEffect(() => {
        if (search !== '') {
            setProjectArray(projectArray
                .filter((value: ProjectModel) => value.title.includes(search)));
        } else {
            setProjectArray(array);
        }
    }, [search])

    return (
        <div>
            <h1 className="sectionTitle">My projects</h1>
            <hr/>
            <label htmlFor="projectSearchBar">Search project</label><br/>
            <input onChange={handleChange} value={search} id="projectSearchBar" type="text"/>
            <div className="projectList">
                {
                    projectArray
                        .map((project, index) => {
                            return <ProjectCard key={index} card={project}/>
                        })
                }
            </div>
        </div>
    );
}
