import "./timelinecard.css"
import {FunctionComponent} from "react";
import {ResumeModel} from "../../../model/ResumeModel";

export const TimelineCard:FunctionComponent<{card: ResumeModel}> = (children, card) => {

    return(
        <div className="card">
            <div className="general">
                <h1>{children.card.title}</h1>
                <h2>{children.card.year}</h2>
                <hr/>
                <p>{children.card.content}</p>
                <hr/>
                <a href="#">Learn More</a>
            </div>
        </div>
    );

}
